@import url(https://fonts.googleapis.com/css?family=Montserrat|Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

document {
  background-color: #fafafa;
}

.App {
  display: flex;
  justify-content: center;
  font-family: Montserrat;
}

h1 {
  font-family: Nunito;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 70px;
  color: #456b8a;
  margin-top: 0;
  margin-bottom: 0;
}

h3 {
  font-family: Montserrat;
  font-size: 20px;
  color: #456b8a;
  font-weight: 400;
}

.profile-pic {
  height: 12rem;
  /* margin: auto; */
  display: block;
}

.header-info {
  color: #575757;
}

.header {
  margin-top: 2rem;
  margin-bottom: 4.5em;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}

.header-line {
  height: 5em;
  background-color: #456b8a;
  width: 0.4em;
  float: right;
}

.text-holders {
  display: flex;
  margin-bottom: 4.5em;
  justify-content: space-between;
}

ul {
  list-style-type: none;
  padding: 0;
}

.text-column {
  width: 20%;
  flex-direction: column;
  justify-content: space-between;
  height: 27rem;
  position: relative;
}

.text-element-bottom {
  align-self: flex-end;
  position: absolute;
  bottom: 0;
}

.text-element-top {
  align-self: flex-start;
}

.project img {
  width: 100%;
  border: none;
  display: block;
}

.project {
  color: white;
  background-repeat: no-repeat;
  margin-bottom: 4rem;
  position: relative;
}

.project h2 {
  margin-bottom: 0;
}

.ara {
  background-image: url("/static/media/Group 2.8cd3da4b.png");
  border-right: 6px solid #03ab00;
}

.ara h2 {
  color: #03ab00;
}

.floater {
  background-image: url("/static/media/Group 3.7baa3e2c.png");
  border-left: 6px solid #c8a6cd;
}

.floater h2 {
  color: #c8a6cd;
}

.ara-dm {
  background-image: url("/static/media/Group 4.f7ac41d0.png");
  border-right: 6px solid #800402;
}

.ara-dm h2 {
  color: #a70300;
}

.align-right {
  text-align: end;
  right: 1em;
}

.align-left {
  left: 1em;
}

.sound {
  border-left: 6px solid #da0027;
}

.sound h2 {
  color: #da0027;
}

.stock {
  border-right: 6px solid #62efff;
}

.stock h2 {
  color: #62efff;
}

.project-content {
  width: 50%;
  position: absolute;
  top: 0;
}

@media only screen and (max-width: 808px) {
  h1 {
    font-size: 28px;
  }
  h3 {
    font-size: 10px;
  }
  .profile-pic {
    height: 8rem;
    display: block;
  }
  body {
    font-size: 8px;
  }
  h2 {
    font-size: 10px;
  }
  .project {
    margin-bottom: 2rem;
  }
  .ara {
    border-right: 4px solid #03ab00;
  }
  .floater {
    border-left: 4px solid #ff7557;
  }
  .ara-dm {
    border-right: 4px solid #62efff;
  }
  .sound {
    border-left: 4px solid #da0027;
  }
  .stock {
    border-right: 4px solid #62efff;
  }
}

.footer {
    background-color: black;
    color: white;
    width: 100%;
    height: 2.5rem;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-content: center;
}

.footer a {
    text-decoration: none;
    color: #62EFFF;
}
